//500Î∞ÄÎ¶¨ÏÑ∏Ïª®Îìú ÏïàÏóê ÎèôÏûë Ïù¥Î≤§Ìä∏Î•º ÎÑ£Ïñ¥ÎÜ®ÎäîÎç∞
//ÎßåÏïΩ Í∑∏ ÏïàÏóê Ïä§ÌÅ¨Î°§ Ïù¥Î≤§Ìä∏Í∞Ä ÏùºÏñ¥ÎÇòÎ©¥ ÌÅ¥Î¶¨Ïñ¥ ÌÉÄÏûÑÏïÑÏõÉÏúºÎ°ú ÏÖã ÌÉÄÏûÑÏïÑÏõÉÏùÑ Î¨¥Ìö®ÌôîÌïòÏó¨ Í∑∏ Ï†ÑÏùò Ïù¥Î≤§Ìä∏Îäî Ï¥àÍ∏∞ÌôîÍ∞Ä ÎêòÎäî ÏõêÎ¶¨Ïù∏ÎìØ

