window.addEventListener('scroll', debounce(60)); 

function debounce(delay) {
    let controlTime;
    console.log(window.scrollY);
    
    return function () {
        clearTimeout(controlTime);
        controlTime = setTimeout(() => {
            console.log(window.scrollY);
            quickMenu.style.top = `${window.scrollY + quickMenuLocate}px`;
        }, delay);
    }
}


